* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: 800px;
  height: 600px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-radius: 10%;
  margin: 0 auto;
  transition: background-color 1s, border-color 3s;
}

.buttons {
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

.button {
  cursor: pointer;
  border-radius: 10px;
  padding: 15px;
}

.div_timer {
  width: 800px;
  background-color: #d3d3d3;
  border: 1px solid;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.calendar {
  padding-top: 60px;
}

.timer {
  text-align: center;
  gap: 30px;
  margin: 50px auto;
  display: flex;
}

.field {
  flex-direction: column;
  display: flex;
}

.value {
  font-family: monospace;
  font-size: 40px;
}

.label {
  text-transform: uppercase;
}

